@import url('http://fonts.cdnfonts.com/css/roboto');
@import url('http://fonts.cdnfonts.com/css/arial');

.youtube {
  overflow: hidden;
  width: 100%;
  height: 100vh;
  background-color: #181818;
  font-family: 'Roboto', 'Arial', sans-serif;
  color: #fff;
}
.content {
  display: flex;
  flex: 1 1 100%;
  position: relative;
  width: 100%;
  height: 100%;
}
.container {
  overflow-y: auto;
  width: 100%;
  margin-top: 60px;
}

@media screen and (max-width: 991px) {
  .content {
    flex-direction: column;
  }
}

body {
  padding: 0;
  margin: 0;
  overflow-y: scroll;
}
body.autoscroll {
  overflow-y: auto;
}
body.no-scroll {
  overflow: hidden;
}
body.no-y-scroll {
  overflow-y: hidden;
}
.hidden {
  display: none;
}
textarea {
  --paper-input-container-input_-_white-space: pre-wrap;
}
.grecaptcha-badge {
  visibility: hidden;
}
